import { Container } from '@mui/material';
// routes
import { PATH_ADMIN } from '../../routes/paths';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// sections
import BankLockInt from '../../sections/admin/BankLockInt';

export default function BankLock() {
    const { themeStretch } = useSettings();

    return (
        <Page title="Single Bank Lock">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading="Single Bank Lock"
                    links={[
                        { name: 'Dashboard', href: PATH_ADMIN.general.app },
                        { name: 'Lock / Unlock', href: '#' },
                        { name: 'Single Bank Lock' },
                    ]}
                />
                <BankLockInt />
            </Container>
        </Page>
    );
}
