import { useState, useEffect } from 'react';
import { useSnackbar } from 'notistack';
// @mui
import {
    Box,
    Card,
    Table,
    Button,
    TableRow,
    TableBody,
    TableCell,
    Container,
    TableContainer,
    TableHead,
    Typography,
    Switch,
    Stack,
    CircularProgress,
} from '@mui/material';
import axios from '../../utils/axios';

export default function BankLockInt() {
    const { enqueueSnackbar } = useSnackbar();
    const [providers, setProviders] = useState([]);
    const [globalLock, setGlobalLock] = useState(false);
    const [fetching, setFetching] = useState(true);
    const accessToken = window.localStorage.getItem('accessToken');

    const fetchSettings = async () => {
        try {
            setFetching(true);
            const response = await axios.get(`/api/secure/trans/settings/${accessToken}/habukhan/secure`);
            if (response.data.status === 'success') {
                setProviders(response.data.providers);
                setGlobalLock(!!response.data.settings?.transfer_lock_all);
            }
        } catch (error) {
            console.error(error);
        } finally {
            setFetching(false);
        }
    };

    useEffect(() => {
        fetchSettings();
    }, []);

    const handleToggleLock = async (slug, currentStatus) => {
        try {
            const action = currentStatus ? 'unlock' : 'lock';
            const response = await axios.post(`/api/secure/lock/bank/${accessToken}/habukhan/secure`, {
                slug,
                action,
            });

            if (response.data.status === 'success') {
                enqueueSnackbar(response.data.message);
                fetchSettings(); // Refresh list
            }
        } catch (error) {
            enqueueSnackbar('Failed to update provider status', { variant: 'error' });
        }
    };

    const handleToggleGlobalLock = async () => {
        try {
            const action = globalLock ? 'unlock' : 'lock';
            const response = await axios.post(`/api/secure/toggle/globaltransferlock/${accessToken}/habukhan/secure`, {
                action,
            });

            if (response.data.status === 'success') {
                enqueueSnackbar(response.data.message);
                setGlobalLock(!globalLock);
            }
        } catch (error) {
            enqueueSnackbar('Failed to update global lock status', { variant: 'error' });
        }
    };

    if (fetching) {
        return (
            <Box display="flex" justifyContent="center" sx={{ p: 5 }}>
                <CircularProgress />
            </Box>
        );
    }

    return (
        <Stack spacing={3}>
            <Card sx={{ p: 3, bgcolor: globalLock ? 'error.lighter' : 'success.lighter' }}>
                <Stack direction="row" alignItems="center" justifyContent="space-between">
                    <Box>
                        <Typography variant="h6" color={globalLock ? 'error.main' : 'success.main'}>
                            Global External Transfer Lock
                        </Typography>
                        <Typography variant="body2" color="text.secondary">
                            {globalLock
                                ? 'All external bank transfers are currently DISABLED system-wide.'
                                : 'External bank transfers are ACTIVE. Individual provider locks still apply.'}
                        </Typography>
                    </Box>
                    <Switch
                        checked={globalLock}
                        onChange={handleToggleGlobalLock}
                        color="error"
                    />
                </Stack>
            </Card>

            <Card sx={{ p: 3 }}>
                <Typography variant="h6" gutterBottom>
                    Individual Bank Provider Locks
                </Typography>
                <TableContainer>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell>Provider Name</TableCell>
                                <TableCell align="center">Status</TableCell>
                                <TableCell align="right">Action</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {providers.map((row) => (
                                <TableRow key={row.slug}>
                                    <TableCell>{row.name}</TableCell>
                                    <TableCell align="center">
                                        <Typography
                                            variant="caption"
                                            sx={{
                                                color: row.is_locked ? 'error.main' : 'success.main',
                                                fontWeight: 'bold',
                                            }}
                                        >
                                            {row.is_locked ? 'LOCKED' : 'ACTIVE'}
                                        </Typography>
                                    </TableCell>
                                    <TableCell align="right">
                                        <Switch
                                            checked={!row.is_locked}
                                            onChange={() => handleToggleLock(row.slug, row.is_locked)}
                                            color="success"
                                        />
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Card>
        </Stack>
    );
}
